footer {
    background-color: $black;
    padding: 30px 0; 

    .brand-name {
        margin-bottom: 35px;
        &__logo {
            align-self: center;
        }
        &__title {
            align-items: center;
            color: #fff;
        }
    }

    .contacts {
        display: block;
        .contact {
            margin-bottom: 5px;

            &:last-child {
                margin-bottom: 0;
            }

            p {
                color: #fff;
            }
        }
    }

    .footer-content {
        justify-content: space-between;

        &__menu {
            li {
                margin: 15px 0; 
                a {
                    color: #fff;
                    font-weight: 700;
                    font-size: 18px;
                }
            }
            .sub-menu {
                li {
                    margin: 5px 0;
                    a {
                        font-size: 16px;
                        font-weight: 400;
                    }
                }
            }
        }
    }

    hr {
        margin-top: 35px;
        display: block;
        height: 1px;
        border: 0;
        border-top: 1px solid #000;
    }

    .copyright {
        padding-top: 15px;
        display: flex;
        justify-content: space-between;

        &__text {
            align-self: center;
        }

        .call-buttons {
            align-self: center;
        }

        p {
            color: #fff;
        }
    }
}